<template>
    <div id="edit-employee">	        
        <h3>Edit Employee</h3>
        <div class="row">
          <form @submit.prevent="updateEmployee" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model.lazy="employee.employee_id" required disabled>                
              </div>
              <div class="input-field col s12">
                <input type="text" v-model.lazy="employee.name" required>                
              </div>
              <div class="input-field col s12">
                <input type="text" v-model.lazy="employee.dept" required>                
              </div>
              <div class="input-field col s12">
                <input type="text" v-model.lazy="employee.position" required>                
              </div>
              <button type="submit" class="btn">Submit</button>
              <router-link class="btn grey" to="/">Cancel</router-link>
            </div>
          </form>
        </div>    
    </div>
</template>
<script>
import db from "../firebase/firebaseinit.js";
export default {
  name: "edit-employee",
  data() {
    return {
      employee: {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    };
  },
  beforeRouteEnter(to, from, next) {
    db
      .collection("employee")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            const data = doc.data();
            vm.employee.employee_id = data.employee_id;
            vm.employee.name = data.name;
            vm.employee.dept = data.dept;
            vm.employee.position = data.position;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db
        .collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee.employee_id = doc.data().employee_id;
            this.employee.name = doc.data().name;
            this.employee.dept = doc.data().dept;
            this.employee.position = doc.data().position;
          });
        });
    },
    updateEmployee() {
      db
        .collection("employee")
        .where("employee_id", "==", this.employee.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => doc.ref.update(this.employee));
          console.log(this.employee.employee_id);
          this.$router.push({
            name: "view-employee",
            params: { employee_id: this.employee.employee_id }
          });
        });
    }
  }
};
</script>
<style scoped>
</style>





