<template>
    <div id="dashboard">
        <h3>Dashboard</h3>
        <ul class="collection with-header">
          <li class="collection header">
            <h4>Employees</h4>
          </li>
          <li v-for="employee in employees" :key="employee.id" class="collection-item">
            <div class="chip">
              {{ employee.dept }}
            </div>
              {{ employee.employee_id }} : {{ employee.name }}              
              <router-link class="secondary-content" :to='{ name:"view-employee" , params:{ employee_id: employee.employee_id } } '><i class="fa fa-eye"></i></router-link>
          </li>
        </ul>
        <div class="fixed-action-btn">
          <router-link to="/new" class="btn-floating btn-large red"><i class="fa fa-plus"></i></router-link>          
        </div>
    </div>
</template>
<script>
import db from "../firebase/firebaseinit.js";
export default {
  name: "dashboard",
  data() {
    return {
      employees: []
    };
  },
  created() {
    db
      .collection("employee")
      .orderBy("dept")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const object = doc.data();
          const data = {
            id: doc.id,
            employee_id: object.employee_id,
            name: object.name,
            dept: object.dept,
            position: object.position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>
<style scoped>
</style>





