<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
          <form @submit.prevent="saveEmployee" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model.lazy="newEmployee.employee_id" required>
                <label >Employee ID#</label>
              </div>
              <div class="input-field col s12">
                <input type="text" v-model.lazy="newEmployee.name" required>
                <label >Name</label>
              </div>
              <div class="input-field col s12">
                <input type="text" v-model.lazy="newEmployee.dept" required>
                <label >Dept</label>
              </div>
              <div class="input-field col s12">
                <input type="text" v-model.lazy="newEmployee.position" required>
                <label >Position</label>
              </div>
              <button type="submit" class="btn">Submit</button>
              <router-link class="btn grey" to="/">Cancel</router-link>
            </div>
          </form>
        </div>
    </div>
</template>
<script>
import db from "../firebase/firebaseinit.js";
export default {
  name: "new-employee",
  data() {
    return {
      newEmployee: {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    };
  },
  methods: {
    saveEmployee() {
      db
        .collection("employee")
        .add(this.newEmployee)
        .then(documentReference => {
          console.log(documentReference);
          console.log(this.newEmployee);
          this.$router.push(`/${this.newEmployee.employee_id}`);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style scoped>
</style>





