<template>
    <div id="view-employee">
        <ul class="collection with-header">
          <li class="collection-header">
            <h4>{{ name }}</h4>
          </li>
          <li class="collection-item">Employee Id#: {{ employee_id }}</li>
          <li class="collection-item">Department: {{ dept }}</li>
          <li class="collection-item">Position: {{ position }}</li>
        </ul>
        <router-link class="btn grey" to="/">Back</router-link>
        <button class="btn red" @click="deleteEmployee">Delete</button>
        <div class="fixed-action-btn">
          <router-link :to="{ name : 'edit-employee' , params: { employee_id : employee_id } }" class="btn-floating btn-large red"><i class="fa fa-pencil-alt"></i></router-link>          
        </div>
    </div>
</template>
<script>
import db from "../firebase/firebaseinit.js";
export default {
  name: "view-employee",
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db
      .collection("employee")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            const data = doc.data();
            vm.employee_id = data.employee_id;
            vm.name = data.name;
            vm.dept = data.dept;
            vm.position = data.position;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db
        .collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee_id = doc.data().employee_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          });
        });
    },
    deleteEmployee() {
      if (confirm("Are you srue?")) {
        db
          .collection("employee")
          .where("employee_id", "==", this.employee_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => doc.ref.delete());

            this.$router.push("/");
          });
      }
    }
  },
  filters: {
    padLeft(value, padChar, len) {
      return;
    }
  }
};
</script>
<style scoped>
</style>





